import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import { fetchGames } from '../actions/';
import '../css/browse.css';
import Loading from './Loading';

class Browse extends React.Component {

    componentDidMount() {
        this.props.fetchGames();
    }

    renderGamesList = () => {
      const games = this.props.games;

      const gamesHtml = games.map(game => {
          const thumbnail = game.box_art_url.replace('{width}', '285').replace('{height}', '380');
          return (
              <Link className={"game-link mr-3 my-2"} to={`/game-tv/streams/${game.id}`} key={game.id}>
                  <figure className="figure">
                      <img src={ thumbnail } className="figure-img img-fluid rounded"
                           alt={ game.name }/>
                          <figcaption className="figure-caption text-left text-custom"> { game.name }</figcaption>
                  </figure>
              </Link>
          )
      });

        return gamesHtml;
    };

    render() {

        if(!this.props.games) {
            return ( <Loading/>)
        } else {


            return (
                <div className={"container-fluid"}>
                    <div className="container">
                        <h1 className={"text-custom mt-4"}>Browse Games</h1>

                        <div className={"d-flex flex-wrap"}>
                            { this.renderGamesList() }
                        </div>
                    </div>

                </div>
            )
        }
    }
};

const mapStateToProps = (state) => {
  return {
      games: state.games.games
  }
};

export default connect(mapStateToProps, { fetchGames })(Browse);