
export default (state = {}, action) => {
    switch (action.type) {
        case 'FETCH_GAMES':
            return { ...state, 'games': action.payload};
        case 'FETCH_GAME_STREAMER':
            return { ...state, 'gameStreamers': action.payload};
        case 'GAME_SEARCH':
            return { ...state, 'gameResults': action.payload};
        default:
            return state;
    }
}