import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import Results from './Results';
import '../css/navbar.css';
import { fetchGameResults } from "../actions";

class Navbar extends React.Component {
    constructor(props){
        super(props);
        this.state = {val: ''}
    }
    handleSearch = (e) => {
        this.props.fetchGameResults(e.target.value);
        this.setState({val: e.target.value});
    };

    handleFocusLoss = () => {
        setTimeout(() => {this.setState({val: ''})}, 500);
    };

    render(){
        return (
            <div>
                <nav className={"navbar navbar-expand-lg"}>
                    <Link to={"/game-tv"} className="navbar-brand text-white">GameTV</Link>
                    <button className="navbar-toggler" type={"button"} data-toggle="collapse" data-target="#nav-collapse">
                        <i className={"fas fa-bars text-custom"}></i>
                    </button>

                    <div className={"collapse navbar-collapse"} id="nav-collapse">
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item">
                                <Link to={"/game-tv"} className="nav-link text-white">Home</Link>
                            </li>
                            <li className="nav-item">
                                <Link to={"/game-tv/browse"} className="nav-link text-white">Browse</Link>
                            </li>
                            <li className="nav-item">
                                <Link to={"/game-tv/favorites"} className="nav-link text-white">Favorites</Link>
                            </li>
                        </ul>
                        <form className="form-inline my-2 my-lg-0 position-relative w-100 d-none d-lg-block" onSubmit={(e) => e.preventDefault()} style={{"maxWidth": "300px"}}>
                            <input className="form-control form-control-sm w-100" type="search" placeholder="Search Games" aria-label="Search" onChange={this.handleSearch} value={this.state.val} onBlur={this.handleFocusLoss}/>
                            {this.state.val ? <Results results={this.props.results ? this.props.results : ""}/> : ''}

                        </form>
                    </div>
                </nav>

            </div>
        )
    }

};

const mapStateToProps = state => {
    return {
        results: state.games.gameResults
    }
};

export default connect(mapStateToProps, { fetchGameResults })(Navbar);