import twitch from '../api/twitch';



//STREAMS

export const featureStream = (stream) => {
    return {
        type: 'FEATURED_STREAM',
        payload: stream
    }
};

export const popularThree = (streams) => {
    return {
        type: 'POPULAR_STREAMS',
        payload: streams
    }
};

export const getStream = (stream) => {
    return {
        type: 'GET_STREAM',
        payload: stream
    }
};

export const featuredStream = () => async dispatch => {
    const response = await twitch.get("/streams?first=1");

    dispatch(featureStream(response.data.data[0]));
};

export const popularThreeStreams = () => async dispatch => {
    const response = await twitch.get("/streams?first=4");

    dispatch(popularThree(response.data.data));
};

export const fetchStream = (id) => async dispatch => {
    const response = await twitch.get(`/streams?user_id=${id}`);

    dispatch(getStream(response.data.data[0]));
};


//GAMES


const getGames = (games) => {
    return {
        type: 'FETCH_GAMES',
        payload: games
    }
};

export const fetchGames = () => async dispatch => {
    const response = await twitch.get('/games/top?first=50');

    dispatch(getGames(response.data.data));
};

const getGameStreamers = stream => {
    return {
        type: 'FETCH_GAME_STREAMER',
        payload: stream
    }
};

export const fetchGameStreamers = (id) => async dispatch => {
    const response = await twitch.get(`/streams?game_id=${id}&first=50`);

    dispatch(getGameStreamers(response.data.data));
};

const gameSearch = (games) => {
    return {
        type: 'GAME_SEARCH',
        payload: games
    }
};

export const fetchGameResults = (name) => async dispatch => {
    const response = await twitch.get(`/games?name=${name}`);

    dispatch(gameSearch(response.data.data));
};


//FAVORITES

const addFavorite = stream => {
    return {
        type: 'ADD_FAVORITE',
        payload: stream
    }
};

export const addToFavorites = (id) => async dispatch => {
    const response = await twitch.get(`/streams?user_id=${id}`);

    dispatch(addFavorite(response.data.data));
};

const deleteFavorite = id => {
    return {
        type: 'DELETE_FAVORITE',
        payload: id
    }
};

export const deleteFromFavorites = id => {
    return deleteFavorite(id)
};