import React from 'react';
import './App.css';
import './css/general.css';
import './css/loaders.min.css';
import Navbar from './components/Navbar';
import Home from './components/Home';
import Browse from './components/Browse';
import Favorites from './components/Favorites';
import Stream from './components/Stream';
import BrowseStreamers from "./components/BrowseStreamers";

import { BrowserRouter, Route} from 'react-router-dom';

function App() {
  return (
      <BrowserRouter>
        <div>
          <Navbar/>

          <Route path={"/game-tv/"} exact component={Home}/>
          <Route path={"/game-tv/browse"} component={Browse}/>
          <Route path={"/game-tv/streams/:id"} component={BrowseStreamers}/>
          <Route path={"/game-tv/favorites"} component={Favorites}/>
          <Route path={"/game-tv/stream/:id"} component={Stream}/>

        </div>
      </BrowserRouter>
  );
}

export default App;
