import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import { popularThreeStreams } from "../actions";
import Loading from './Loading';

class Popular extends React.Component {
    componentDidMount() {
        this.props.popularThreeStreams();
    }

    renderStreams = () => {
        const streams = this.props.popular;

        const streamHtml = streams.map((stream, i) => {
            if(i === 0) return '';
            let thumbnail = stream.thumbnail_url.replace('{width}', '800').replace('{height}', '525');
            return (
                <div className="col-md-4" key={stream.id}>
                    <div className="card border-0 h-100 popular-card" style={{'backgroundColor': 'transparent'}}>
                        <img className="card-img-top" src={ thumbnail } alt={ stream.user_name }/>
                        <div className="card-body pl-0 h-100 d-flex flex-column">
                            <h4 className="card-title text-custom">{ stream.user_name }</h4>
                            <p className="card-text text-custom">{ stream.title }</p>
                            <h5 className={"card-text text-custom font-weight-normal small"}>Viewers: { stream.viewer_count }</h5>
                            <Link to={`/game-tv/stream/${stream.user_id}`} className="btn btn-outline-danger align-self-start mt-2">View Stream</Link>
                        </div>
                    </div>
                </div>
            )
        });

        return streamHtml;
    };

    render(){

        if(!this.props.popular) {
            return <Loading/>
        } else {
            return (
                <div className="container-fluid py-5">
                    <div className={"container py-3"}>
                        <h2 className={"py-3 text-custom"}>Popular Streamers</h2>
                        <div className="row">
                            { this.renderStreams() }
                        </div>
                    </div>
                </div>
            )
        }

    }

};


const mapStateToProps = state => {
    return {
        popular : state.streams.popularThree
    }
};

export default connect(mapStateToProps, { popularThreeStreams })(Popular);