import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import { fetchGameStreamers } from '../actions/';
import Loading from './Loading';

class BrowseStreamers extends React.Component {

    componentDidMount() {
        this.props.fetchGameStreamers(this.props.match.params.id);
    }

    renderGameStreamersList = () => {
      const streams = this.props.gameStreamers;

      const streamsHtml = streams.map(stream => {
          const thumbnail = stream.thumbnail_url.replace('{width}', '800').replace('{height}', '525');
          return (
              <Link className={"stream-link mr-3 my-2 align-self-start"} to={`/game-tv/stream/${stream.user_id}`} style={{"maxWidth": "250px"}} key={stream.id}>
                  <figure className="figure">
                      <img src={ thumbnail } className="figure-img img-fluid rounded"
                           alt={ stream.user_name }/>
                          <figcaption className="figure-caption text-left text-custom"> { stream.user_name }</figcaption>
                          <figcaption className="figure-caption text-left text-custom"> { stream.title }</figcaption>
                          <figcaption className="figure-caption text-left text-custom"> Viewers: { stream.viewer_count }</figcaption>
                  </figure>
              </Link>
          )
      });

        return streamsHtml;
    };

    render() {

        if(!this.props.gameStreamers) {
            return ( <Loading/>)
        } else {


            return (
                <div className={"container-fluid"}>
                    <div className="container">
                        <h1 className={"text-custom mt-4"}>Browse Streamers</h1>

                        <div className={"d-flex flex-wrap"}>
                            { this.renderGameStreamersList() }
                        </div>
                    </div>

                </div>
            )
        }
    }
};

const mapStateToProps = (state) => {
  return {
      gameStreamers: state.games.gameStreamers
  }
};

export default connect(mapStateToProps, { fetchGameStreamers })(BrowseStreamers);