import React from 'react';
import { connect } from 'react-redux';

import '../css/general.css';
import { featuredStream } from "../actions";
import {Link} from "react-router-dom";
import Loading from './Loading';

class FeaturedVideo extends React.Component {
    componentDidMount() {
        this.props.featuredStream();
    }

    render(){

        if(!this.props.stream) {
            return (
                <Loading/>
            )
        } else {
            return (
                <div className="container-fluid pt-5">
                    <div className="container">
                        <h1 className={"text-custom mb-3"}>Featured Stream</h1>
                        <div className="embed-responsive embed-responsive-16by9 mx-auto">
                            <iframe className="embed-responsive-item" src={`https://player.twitch.tv/?channel=${this.props.stream.user_name}&muted=true`}
                                    allowFullScreen title={'Featured Stream'}/>
                        </div>
                        <div>
                            <h3 className={"mt-3 text-custom"}>{this.props.stream.user_name}</h3>
                            <h4 className={"text-custom font-weight-normal"}>{this.props.stream.title }</h4>
                            <h5 className={"text-custom font-weight-normal mb-3 h6"}>Viewer Count: { this.props.stream.viewer_count }</h5>
                            <Link to={`/game-tv/stream/${this.props.stream.user_id}`} className="btn btn-outline-danger mt-auto align-self-start">View Stream</Link>
                        </div>
                    </div>
                </div>
            )
        }

    }

};

const mapStateToProps = state => {
    return {
        stream: state.streams.featured
    }
};

export default connect(mapStateToProps, { featuredStream })(FeaturedVideo);