import React from 'react';
import { connect } from 'react-redux';

import { fetchStream, addToFavorites, deleteFromFavorites } from '../actions';
import Loading from './Loading';


class Stream extends React.Component {

    componentDidMount() {
        this.props.fetchStream(this.props.match.params.id);
    }

    handleAddFavoriteClick = () => {
        this.props.addToFavorites(this.props.match.params.id);
    };

    handleDeleteFavoriteClick = () => {
        this.props.deleteFromFavorites(this.props.match.params.id);
    };
    render(){
        if(!this.props.stream){
            return <Loading/>
        } else if(this.props.favorite) {
            return (
                <div className="container-fluid">
                    <div className="container">
                        <h1 className={"text-custom mt-3 d-flex justify-content-between align-items-center"}>Live Stream <i className={"fas fa-heart"} onClick={this.handleDeleteFavoriteClick} style={{'cursor': 'pointer'}}/></h1>
                        <div className="row pt-3">
                            <div className="col-md-12 col-lg-8 mb-md-5">
                                <div className="embed-responsive embed-responsive-16by9 mx-auto">
                                    <iframe className="embed-responsive-item" src={`https://player.twitch.tv/?channel=${this.props.stream.user_name}&muted=true`}
                                            allowFullScreen title={'Featured Stream'}/>
                                </div>
                                <h2 className={"text-custom h3 mt-2"}>{ this.props.stream.user_name }</h2>
                                <h3 className={"text-custom h5 mt-2"}>{ this.props.stream.title }</h3>
                                <p className={"text-custom"}>Current Viewers: { this.props.stream.viewer_count }</p>
                            </div>
                            <div className="col-md-12 col-lg-4">
                                <iframe frameBorder="<frameborder width>"
                                        scrolling="<scrolling>"
                                        id={this.props.stream.user_name}
                                        src={`https://www.twitch.tv/embed/${this.props.stream.user_name}/chat`}
                                        height={'500px'}
                                        width="100%"
                                        title={this.props.stream.title}
                                >
                                </iframe>
                            </div>
                        </div>
                    </div>
                </div>
            )
        } else {
            return (
                <div className="container-fluid">
                    <div className="container">
                        <h1 className={"text-custom mt-3 d-flex justify-content-between align-items-center"}>Live Stream <i className={"far fa-heart text-custom"} onClick={this.handleAddFavoriteClick} style={{'cursor': 'pointer'}}/></h1>
                        <div className="row pt-3">
                            <div className="col-md-12 col-lg-8 mb-md-5">
                                <div className="embed-responsive embed-responsive-16by9 mx-auto">
                                    <iframe className="embed-responsive-item" src={`https://player.twitch.tv/?channel=${this.props.stream.user_name}&muted=true`}
                                            allowFullScreen title={'Featured Stream'}/>
                                </div>
                                <h2 className={"text-custom h3 mt-2"}>{ this.props.stream.user_name }</h2>
                                <h3 className={"text-custom h5 mt-2"}>{ this.props.stream.title }</h3>
                                <p className={"text-custom"}>Current Viewers: { this.props.stream.viewer_count }</p>
                            </div>
                            <div className="col-md-12 col-lg-4">
                                <iframe frameBorder="<frameborder width>"
                                        scrolling="<scrolling>"
                                        id={this.props.stream.user_name}
                                        src={`https://www.twitch.tv/embed/${this.props.stream.user_name}/chat`}
                                        height={'500px'}
                                        width="100%"
                                        title={this.props.stream.title}
                                >
                                </iframe>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
    }
}

const mapStateToProps = (state, props) => {
  return {
      stream: state.streams.currentStream,
      favorite: state.favorites.find(item => {return item.user_id === props.match.params.id})
  }
};

export default connect(mapStateToProps, { fetchStream, addToFavorites, deleteFromFavorites })(Stream);