import React from 'react';
import {Link} from "react-router-dom";
import { connect } from 'react-redux';

import { fetchGameStreamers } from '../actions';

class Results extends React.Component {
    handleClick = (id) => {
        this.props.fetchGameStreamers(id);
    };

    render() {
        if (this.props.results.length) {
            const html = this.props.results.map(item => {
                const thumbnail = item.box_art_url.replace('{width}', '285').replace('{height}', '380');
                return (
                    <Link className={"list-group-item d-flex align-items-center pl-2"}
                          to={`/game-tv/streams/${item.id}`} onClick={() => this.handleClick(item.id)} key={item.id}>
                        <div><img className={"img-fluid rounded"} src={thumbnail} alt={item.name}
                                  style={{"maxHeight": "70px"}}/></div>
                        <p className={"ml-2 text-dark"}>{item.name}</p>
                    </Link>
                )
            });
            return (
                <div className={"list-group results"}>
                    {html}
                </div>
            )
        } else {
            return null;
        }
    }
}

export default connect(null, { fetchGameStreamers })(Results);