import React from 'react';
import { connect } from 'react-redux';
import {Link} from "react-router-dom";
import Loading from './Loading';

class Favorites extends React.Component {
    renderFavorites = () => {
        const html = this.props.favorites.map(favorite => {
            let thumbnail = favorite.thumbnail_url.replace('{width}', '800').replace('{height}', '525');
            return (
                <div className="col-sm-3" key={favorite.id}>
                    <div className="card border-0 h-100" style={{'backgroundColor': 'transparent'}}>
                        <img className="card-img-top" src={ thumbnail } alt={ favorite.user_name }/>
                        <div className="card-body pl-0 h-100 d-flex flex-column justify-content-between">
                            <h4 className="card-title text-custom">{ favorite.user_name }</h4>
                            <p className="card-text text-custom">{ favorite.title }</p>
                            <h5 className={"card-text text-custom font-weight-normal small"}>Viewers: { favorite.viewer_count }</h5>
                            <Link to={`/game-tv/stream/${favorite.user_id}`} className="btn btn-outline-danger align-self-start mt-2">View Stream</Link>
                        </div>
                    </div>
                </div>
            )
        });

        return html;
    };

    render() {
        if(this.props.favorites.length === 0 ){
            return (
                <div className={"container pt-3"}>
                    <h1 className={"mb-3 text-custom"}>Favorites</h1>
                    <p className={"lead text-custom"}>You have not favorited any streams! Click the favorite heart to save the stream to your favorites!</p>
                </div>
            )
        } else if(this.props.favorites.length > 0){
            return (
                <div className={"container pt-3"}>
                    <h1 className={"mb-3 text-custom"}>Favorites</h1>
                    <div className="row">{this.renderFavorites()}</div>

                </div>
            )
        } else {
            return (
                <Loading/>
            )
        }

    }
};

const mapStateToProps = state => {
    return {
        favorites: state.favorites
    }
};

export default connect(mapStateToProps, null)(Favorites);