
export default (state = {}, action) => {
    switch (action.type) {
        case 'FEATURED_STREAM':
            return { ...state, 'featured': action.payload};
        case 'POPULAR_STREAMS':
            return { ...state, 'popularThree': action.payload};
        case 'GET_STREAM':
            return { ...state, 'currentStream': action.payload};
        default:
            return state;
    }
};